import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

import { IBooking } from '../../../../services/data/bookings.service';
import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/globals/base.component';
import { IRoom } from '../../../../services/data/rooms.service';

import * as dayjs from 'dayjs';

/** Details about the associated event overlapping with other events */
export interface IEventOverlap {
    /** Index of this item with the overlapping items */
    index: number;
    /** Total number of items overlapping with this one */
    total: number;
}

@Component({
    selector: 'day-view-space-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss']
})
export class DayViewSpaceEventComponent extends BaseComponent implements OnInit, OnChanges {
    /** Event to display */
    @Input() public event: IBooking;
    /** Event to display */
    @Input() public space: IRoom;
    /** Overlap details for the event */
    @Input() public overlap: IEventOverlap;

    /** Position of the event start time in the day */
    public top: number;
    /** Position of the event relative to overlapping events */
    public left: number;
    /** Height based of the duration of the event */
    public height: number;
    /** Size of the event when overlapping with other events */
    public width: number;
    /** Offset from the event's top that the setup time starts */
    public overflow_top: number;
    /** Offset from the event's bottom that the breakdown time ends */
    public overflow_bottom: number;

    constructor(private _service: AppService) {
        super();
    }

    ngOnInit() {}

    /** Type of event */
    public get type(): string {
        if (this.event) {
            return this.event.state === 'cancelled' ? 'cancelled' : this.event.booking_type;
        }
        return '';
    }

    /** Period that the event will occur during the day */
    public get period(): string {
        if (this.event && this.event.display) {
            return `${this.event.display.start} – ${this.event.display.end}`
        }
        return ``;
    }

    /** Number attendees expected to turn up to the event */
    public get expected(): number {
        const expected = this.event.expected_attendees || {};
        return this.space ? expected[this.space.id] || 0 : 0;
    }

    /** Whether the meeting has catering attached */
    public get has_catering(): boolean {
        return !!(this.event && this.event.catering && this.event.catering.items && this.event.catering.items.length);
    }

    /** View booking details */
    public view() {
        this._service.Bookings.view(this.event, this.space).then((e: any) => {
        }, _ => null);
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.event || changes.overlap) {
            this.calculatePosition();
        }
    }

    /**
     * View catering order for the event
     */
    public viewOrder() {
        if (this.event && this.event.catering) {
            if (sessionStorage) {
                sessionStorage.setItem('CONCIERGE.view_order', JSON.stringify({ ...this.event, room: { ...this.event.room, bookings: [] } }))
            }
            this._service.navigate(['/catering', 'orders'], { id: this.event.id });
        }
    }

    /** Calculate the position */
    private calculatePosition() {
        if (this.event) {
            const overlap = this.overlap || { index: 0, total: 1 };
            const date = dayjs(this.event.date).startOf('m');
            const start = date.hour() + date.minute() / 60;
            if (this.event.all_day) {
                this.top = 0;
                this.height = 100;
            } else {
                this.top = (start / 24) * 100;
                this.height = ((this.event.duration / 60) / 24) * 100;
            }
            this.width = Math.min(100, 100 / overlap.total + 5);
            this.left = Math.min(100 - this.width, this.width * overlap.index - 5 * overlap.index);
            this.overflow_top = (this.event.setup ? this.event.setup / this.event.duration : -.1) * 100;
            this.overflow_bottom = (this.event.breakdown ? this.event.breakdown / this.event.duration : -.1) * 100;
        }
    }
}
