import { Component, OnInit, HostListener, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { BaseComponent } from '../../shared/globals/base.component';
import { IRoom } from '../../services/data/rooms.service';
import { AppService } from '../../services/app.service';

import * as dayjs from 'dayjs';
import { IBooking } from '../../services/data/bookings.service';

export interface ITimeBlock {
    /** ID of the time block */
    id: string;
    /** Display value for the time block */
    display: string;
}

const UPDATE_INTERVAL = 15;

@Component({
    selector: 'app-day-view',
    templateUrl: './day-view.component.html',
    styleUrls: ['./day-view.component.scss']
})
export class DayViewComponent extends BaseComponent implements OnInit {
    /** List of timeblocks to display */
    public time_blocks: ITimeBlock[] = this.generateTimeBlocks();
    /** Top value of the content scroll */
    public scroll_top: number = 0;
    /** Left value of the content scroll */
    public scroll_left: number = 0;
    /** List of spaces to display for  */
    public spaces: IRoom[] = [];
    /** List of spaces to display for  */
    public filtered_spaces: IRoom[] = [];
    /** Mapping of space ids to whether they are in view */
    public in_view: { [id: string]: boolean } = {};
    /** Percent of the day that is now */
    public now: number;
    /** Timestamp of the active date */
    public date: number;
    /** Whether the active day is today */
    public is_today: boolean;
    /** Currently active hour of the day */
    public active_hour: string;
    /** Mapping of spaces loading booking date */
    public loading: { [id: string]: boolean } = {};
    /** Booking ID to open modal for */
    public view_id: string;
    /** Room type to filter the shown rooms by */
    public room_type: string;
    /** Last update time for the space bookings */
    public last_update: number = 0;

    @ViewChild('scroll_area', { static: true }) private scroll_area: ElementRef<HTMLDivElement>;

    constructor(private _service: AppService, private _route: ActivatedRoute) {
        super();
    }

    public get holding_bay(): IRoom {
        const bld = this._service.Buildings.current();
        if (bld.holding_bay) {
            return this._service.Rooms.item(bld.holding_bay);
        }
        return null;
    }

    public ngOnInit(): void {
        if (!this._service.ready() || !this._service.Rooms.list(true).length) {
            return this.timeout('init', () => this.ngOnInit());
        }
        // Subscribe to changes of the selected level
        this.subscription('active_level', this._service.listen('APP.level', (level_id) => {
            const zone_id = level_id === -1 ? this._service.Buildings.current().id : level_id;
            this.spaces = this._service.Rooms.list(true, [zone_id]) || [];
            const holding_bay = this.holding_bay;
            this.filtered_spaces = this.spaces.filter(i =>
                (!this.room_type || i.zones.indexOf(this.room_type) >= 0) && (!holding_bay || i.id !== holding_bay.id) && i.bookable
            );
            this.updateBookings();
            this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        }));
        // Subscribe to changes of the selected date
        this.subscription('date', this._service.listen('APP.date', (date) => {
            this.date = date;
            this.is_today = dayjs().isSame(dayjs(date), 'd');
            this.updateBookings();
            this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        }));
        this.subscription('room_type', this._service.listen('APP.room_type', (type) => {
            this.room_type = typeof type === 'string' ? type : null;
            const holding_bay = this.holding_bay;
            this.filtered_spaces = this.spaces.filter(i =>
                (!this.room_type || i.zones.indexOf(this.room_type) >= 0) && (!holding_bay || i.id !== holding_bay.id) && i.bookable
            );
        }));
        // Subscribe to route query changes
        this.subscription('route.query', this._route.queryParamMap.subscribe((params) => {
            if (params.has('id')) {
                this.view_id = params.get('id');
            }
        }));
        // Update time
        this.updateTime();
        this.interval('time', () => this.updateTime(), 15 * 1000);
        // Update bookings for visible rooms
        this.updateBookings();
        this.interval('bookings', () => this.updateBookings(), UPDATE_INTERVAL * 1000);
        this.interval('scroll', () => {
            if (this.scroll_area && this.scroll_area.nativeElement) {
                this.updateScroll({ target: this.scroll_area.nativeElement });
            }
        }, 1000)
    }

    /**
     * Update scroll parameters to match the change in the scroll position
     * @param event
     */
    public updateScroll(event) {
        this.scroll_top = event.target.scrollTop;
        this.scroll_left = event.target.scrollLeft;
    }

    /** Generate time blocks to display on the day view */
    private generateTimeBlocks(): ITimeBlock[] {
        const blocks: ITimeBlock[] = [];
        const date = dayjs().startOf('h');
        for (let i = 0; i < 24; i++) {
            blocks.push({
                id: date.hour(i).format('HH:mm'),
                display: date.hour(i).format('h:mm A')
            });
        }
        return blocks;
    }

    /** Update the now line position */
    private updateTime() {
        const time = dayjs();
        this.now = (time.hour() + time.minute() / 60) / 24;
        this.active_hour = time.startOf('h').format('HH:mm');
    }

    /** Update bookings for rooms in view */
    private updateBookings() {
        const visible_rooms = this.spaces.filter(i => this.in_view[i.id] !== false);
        if (visible_rooms.length) {
            visible_rooms.forEach(rm => this.loading[rm.id] = true);
            const time = dayjs(this.date).startOf('d');
            const end = time.endOf('d');
            // const room_ids = visible_rooms.reduce((v, i) => v + (v ? ',' : '') + i.id, '');
            const level = this._service.get('APP.level');
            const zone_ids = [level === -1 ? this._service.Buildings.current().id : level ];
            if (this.room_type) {
                zone_ids.push(this.room_type);
            }
            this._service.Rooms.query({
                // room_ids,
                zone_ids,
                available_from: time.unix(),
                available_to: end.unix()
            }).then((room_list) => {
                this._service.Rooms.clearTimeline(time.valueOf(), end.valueOf()/*, room_ids.split(',') */);
                room_list.forEach(rm => {
                    if (this.view_id) {
                        const booking = rm.bookings.find(i => i.id === this.view_id);
                        if (booking) {
                            this.view(booking, rm);
                        }
                    }
                    this._service.Rooms.updateTimeline(rm.id, rm.bookings);
                });
                delete this.loading;
                this.last_update = dayjs().valueOf();
                this.loading = {};
            });
        }
    }

    /**
     * View booking details
     * @param booking Booking to view the details of
     */
    private view(booking: IBooking, space: IRoom) {
        this._service.Bookings.view(booking, space).then(_ => null, _ => null);
        this.view_id = null;
    }
}
