import { Component, Input, Output, EventEmitter } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';

import { IBooking } from '../../../../services/data/bookings.service';
import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/globals/base.component';

const STATES: { [id: string]: string } = {};

@Component({
    selector: 'approvals-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [style({ opacity: 0, height: 0 }), animate(200, style({ opacity: 1, height: '*' }))]),
            transition(':leave', [style({ opacity: 1, height: '*' }), animate(200, style({ opacity: 0, height: 0 }))]),
        ])
    ]
})
export class ApprovalsViewEventComponent extends BaseComponent {
    /** Event to display */
    @Input() event: IBooking;
    /** Event emitter for event actions */
    @Output() acting = new EventEmitter<boolean>();
    /** Whether approval action is being processed */
    public loading: boolean;
    /** Whether event details are shown */
    public shown: boolean;

    /** Person to notify when external attendees arrive */
    public get notifyee() {
        if (this.event) {
            if (this.event.notify_users && this.event.notify_users.length) {
                return this.event.notify_users[0];
            }
        }
        return 'Concierge';
    }

    /** Description of the meeting */
    public get description(): string {
        if (this.event && this.event.notes) {
            return (this.event.notes.find(i => i.type === 'description') || { message: '' }).message;
        }
        return '';
    }

    /** Whether the meeting has catering attached */
    public get has_catering(): boolean {
        return !!(this.event && this.event.catering && this.event.catering.items && this.event.catering.items.length);
    }

    constructor(private _service: AppService) {
        super();
    }

    /**
     * Approve the meeting for the set room
     */
    public accept() {
        if (!this.event.organiser) { this.event.organiser = {} as any; }
        if (!this.event.display) { this.event.display = {}; }
        this.acting.emit(true)
        this._service.confirm({
            title: 'Approve Meeting',
            message: `Approve ${this.event.organiser.name || this.event.organiser}'s meeting in "${(this.event.room || {} as any).name}" at ${this.event.display.start} for ${this.event.display.duration} on ${this.event.display.date}`,
            icon: 'event_available',
            accept: 'Ok',
            cancel: true
        }, (event) => {
            if (event.type === 'Accept') {
                this.loading = true;
                this._service.Bookings.accept(this.event.id, {
                    comment: '',
                    room_id: this.event.room ? this.event.room.id : (this.event as any).room_id
                }).then(
                    () => {
                        this._service.success('Meeting approved.');
                        this.event.state = 'busy';
                        this._service.Rooms.removeFromTimeline(this.event.room.id, this.event.id);
                        this._service.Rooms.addToTimeline(this.event);
                        STATES[this.event.id] = this.event.state;
                        this._service.set('BOOKING.states', STATES);
                        this.loading = false;
                        this.acting.emit(false);
                    },
                    () => {
                        this.acting.emit(false);
                        this._service.error('Error approving meeting.');
                        this.loading = false;
                    }
                );
            }
            event.close();
        });
    }

    /**
     * Decline meeting for the set room
     */
    public decline() {
        if (!this.event.organiser) { this.event.organiser = {} as any; }
        if (!this.event.display) { this.event.display = {}; }
        this.acting.emit(true);
        this._service.confirm({
            title: 'Decline Meeting',
            message: `Decline ${this.event.organiser.name || this.event.organiser}'s meeting in "${(this.event.room || {} as any).name}" at ${this.event.display.start} for ${this.event.display.duration} on ${this.event.display.date}`,
            icon: 'event_busy',
            accept: 'Ok',
            cancel: true
        }, (event) => {
            if (event.type === 'Accept') {
                this.loading = true;
                const booking: IBooking = this.event;
                booking.state = 'loading';
                this._service.Bookings.decline(booking.id, {
                    booking_id: booking.id,
                    organiser: booking.organiser.email,
                    icaluid: booking.icaluid,
                    start: Math.floor(booking.date / 1000),
                    end: Math.floor(booking.date / 1000) + booking.duration * 60,
                    room_id: booking.room ? booking.room.id : (booking as any).room_id
                }).then(
                    () => {
                        this._service.success('Meeting declined.');
                        this.event.state = 'cancelled';
                        this._service.Rooms.removeFromTimeline(this.event.room.id, this.event.id);
                        this._service.Rooms.addToTimeline(this.event);
                        STATES[this.event.id] = this.event.state;
                        this._service.set('BOOKING.states', STATES);
                        this.loading = false;
                        this.acting.emit(false);
                    },
                    () => {
                        this.acting.emit(false);
                        this._service.error('Error declining meeting.');
                        this.loading = false;
                    }
                );
            }
            event.close();
        });
    }
}