import { Component, OnChanges, Input, SimpleChanges, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';

import { IRoom } from '../../../services/data/rooms.service';
import { IBooking } from '../../../services/data/bookings.service';
import { IEventOverlap } from './event/event.component';
import { BaseComponent } from '../../../shared/globals/base.component';

import * as dayjs from 'dayjs';
import { AppService } from '../../../services/app.service';

@Component({
    selector: 'day-view-space',
    templateUrl: './space.component.html',
    styleUrls: ['./space.component.scss']
})
export class DayViewSpaceComponent extends BaseComponent implements OnChanges {
    /** Date to display events for */
    @Input() public date: number;
    /** Space to display events for */
    @Input() public space: IRoom;
    /** Scroll value of the day view */
    @Input() public scroll: number;
    /** Last update time of the displayed bookings */
    @Input('lastUpdate') public last_update: number;
    /** Whether the space column is in view */
    @Input() public inView: boolean;
    /** Emitter for whether the space column is in view */
    @Output() public inViewChange = new EventEmitter<boolean>();
    /** Mapping of event IDs to overlap details */
    public overlap_details: { [id: string]: IEventOverlap } = {};
    /** Whether to only show meetings with a setup or breakdown time */
    public overflow_only: boolean;
    /** Mapping of whether to show events with a given legend id */
    public legend: { [id: string]: boolean } = {};

    /** Container element for the component contents */
    @ViewChild('view', { static: true }) private view: ElementRef<HTMLDivElement>;

    constructor(private _service: AppService) {
        super();
    }

    /** Events to display for the active date */
    public get events(): IBooking[] {
        const date = dayjs(this.date).format('DD MMM YYYY');
        if (this.space) {
            const bookings = this.space.timeline[date] || [];
            const list = bookings.filter(i => {
                return i.state !== 'cancelled' 
                    ? (!this.overflow_only || i.setup || i.breakdown) && this.legend[i.booking_type] !== false
                    : (!this.overflow_only || i.setup || i.breakdown) && this.legend.declined !== false
            });
            return list;
        }
        return [];
    }

    public ngOnInit(): void {
        this.subscription('breakdown', this._service.listen('APP.breakdown', (state) => {
            this.overflow_only = state;
        }));
        this.subscription('legend', this._service.listen('CONCIERGE.legend', (state) => {
            this.legend = state || {};
        }));
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.scroll) {
            this.checkInView();
        }
        if (changes.last_update) {
            this.timeout('update_overlaps', () => this.calculateOverlaps(), 1000);
        }
    }

    /**
     * Track by function for ngFor optimisation
     * @param index Index of the item
     * @param item Data associated with the item at the given index
     */
    public trackByFn(index: number, item: IBooking) {
        return item.id || index;
    }

    /**
     * Calculate to overlap details for the booking events on the space
     */
    private calculateOverlaps() {
        const events = this.events.sort((a, b) => a.date - b.date || a.duration - b.duration || (a.title || '').localeCompare(b.title || ''));
        events.forEach(i => this.overlap_details[i.id] = { index: 0, total: 1 });
        for (const bkn of events) {
            const bkn_start = dayjs(bkn.date).startOf('m');
            const bkn_end = dayjs(bkn_start).add(bkn.duration, 'm').startOf('m');
            let count = 1;
            let index = 0;
            const collisions: IBooking[] = [];
            for (const cmp of events) {
                if (bkn.id !== cmp.id) {
                    const cmp_start = dayjs(cmp.date).startOf('m');
                    const cmp_end = dayjs(cmp_start).add(cmp.duration, 'm').startOf('m');
                    if ((bkn_start.isSame(cmp_start, 'm') || (bkn_start.isAfter(cmp_start, 'm') && bkn_start.isBefore(cmp_end, 'm'))) ||
                        (bkn_end.isSame(cmp_end, 'm') || (bkn_end.isAfter(cmp_start, 'm') && bkn_end.isBefore(cmp_end, 'm')))) {
                        count++;
                        collisions.push(bkn);
                        if (events.findIndex(i => i.id === bkn.id) > events.findIndex(i => i.id === cmp.id)) {
                            index++;
                        }
                    }
                }
            }
            // collisions.forEach(i => this.overlap_details[i.id].total = count > this.overlap_details[i.id].total ? count : this.overlap_details[i.id].total);
            // const indexes = collisions.reduce((a, v) => {
            //     if (this.overlap_details[v.id].index || this.overlap_details[v.id].index === 0) {
            //         a.push(this.overlap_details[v.id].index);
            //     }
            //     return a;
            // }, []);
            // if (indexes.length > 0) {
            //     for (let n = 0; n < count; n++) {
            //         if (!indexes.find(i => i === n)) { index = n; }
            //     }
            // }
            this.overlap_details[bkn.id] = { index: Math.max(0, index), total: count };
        }
    }

    /**
     * Check whether the space column is with the view of the
     */
    private checkInView() {
        if (this.view && this.view.nativeElement) {
            const el = this.view.nativeElement;
            this.inView = el.offsetLeft + this.scroll >= -240 && el.offsetLeft + this.scroll <= window.innerWidth;
            this.inViewChange.emit(this.inView);
        }
    }
}
